import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { UserService } from 'src/app/api/controllers/User';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { SessionService } from 'src/app/core/session/session.service';

import * as _ from 'underscore';
import * as moment from 'moment';
import _moment from "moment-timezone";
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { MatDrawer } from '@angular/material/sidenav';
import { StaffService } from 'src/app/api/controllers/Staff';

@Component({
  selector: 'app-staff-reports',
  templateUrl: './staff-reports.component.html',
  styleUrls: ['./staff-reports.component.scss']
})
export class StaffReportsComponent implements OnInit {

  pageName: string = "";
	pageDesp: string = "";

	userReportDataList: any;

	userReportData: any = "";

	ItemStartIndex: any = 0;
	ItemEndIndex: any;
	totalItems: any;
	itemLimit: any = 10;

	unitFieldType: string = "unitno";
	unitOrder: boolean = true;

	isDataLoaded: boolean = false;

	unitBlocksData: any;

	columnData: any;
	@ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

	reportData:any = "";
	@ViewChild('matDrawer', {static: true}) matDrawer: MatDrawer;
	blockId = null;
    blockNo: string = "";
    end_date = null;
	start_date = null;
	
	filterSelected:string = "all";
	singleBlock:string = "Select Tower";
	selectedBlockId:number;

	timeZone:any;

	allLink: boolean = false;

	constructor(
		private router: Router,
		private route: ActivatedRoute,
		private userService: UserService,
		private apartmentService: ApartmentService,
		private lookupService: LookupService,
		private sharedService: SharedService,
		private sessionService: SessionService,
		private staffService: StaffService
	) { }

	onGlSearchFilter(event) {
        if(event != ""){
      
          let filtergroup = new jqx.filter();
          let filter_or_operator = 1;
          let filtervalue = event;
          let filtercondition = 'contains';
          let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
          filtergroup.operator = 'or';
          filtergroup.addfilter(filter_or_operator, filterData);
        
          this.datagrid.showfiltercolumnbackground(false);
        
          this.columnData.forEach(item => {
            if(item.datafield != 'Actions') {
            this.datagrid.addfilter(item.datafield, filtergroup, true);
            }
          })
        
          this.datagrid.applyfilters();
          
          }
          else {
          this.datagrid.clearfilters();
          }
		 }
		 
		 filterApply() {
      this.goBack();

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.blockNo;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);
    
      this.datagrid.showfiltercolumnbackground(false);
    
      this.columnData.forEach(item => {
        if(item.datafield == 'blockNo') {
        this.datagrid.addfilter(item.datafield, filtergroup, true);
		}
		if(item.datafield == 'apartmentBlockNumber') {
			this.datagrid.addfilter(item.datafield, filtergroup, true);
			}
      })
    
      this.datagrid.applyfilters();
      
			
			
		  }
		
		  clearFilter() {
			this.blockId = null;
		 this.start_date = null;
     this.end_date = null;
     
     this.datagrid.clearfilters();
			
			this.goBack();
		  }

		  goBack() {
			this.matDrawer.close();
		  }

		  getPrintParams(event){
			this.datagrid.exportdata(event, 'UnitUsersReportData');
		  }

		  getSelectedBlock(event) {
      this.blockId = event[0].apartmentBlockId;
      this.blockNo = event[0].apartmentBlockNumber;
		  }

	getIndexParams(event) {
		this.ItemStartIndex = event.ItemStartIndex;
		this.ItemEndIndex = event.ItemEndIndex;
		// this.itemLimit = event.itemLimit;
	}

	sortUnitData(type) {
		this.unitFieldType = type;
		this.unitOrder = !this.unitOrder;
	}

	getFieldOrderBy(type) {
		if (this.unitFieldType == type) {
			return this.unitOrder ? 'asc' : 'desc';
		}
		else return '';
	}

	isMobileView() {
		return window.innerWidth <= 767 ? 'table-responsive' : '';
	}

	isItemsAvailable() {
		return this.totalItems > 0 ? true : false;
	}

	isNoItemsAvailable() {
		return this.totalItems == 0 ? true : false;
	}

	isListOfCommunityStaffs(){
		return this.pageName == "List of Community Staffs"
	}
	getDate(date) {
		return moment(date).add(this.timeZone.offset, 'hours').format(this.timeZone.time);
	}

	getBlockDetails() {
		//jqx column generating
		var cellsrenderer = (row, column, value) => {
			return '<div class="jqx-custom-inner-cell">' + value + '</div>';
		}

		var columnrenderer = (value) => {
			return '<div style="padding: 14px">' + value + '</div>';
		}

		this.isDataLoaded = false;

		var details = {
			ApartmentID: parseInt(localStorage.getItem('apartmentId')),
			ApartmentBlockID: this.blockId
		}



		this.columnData = [{
			text: 'StaffNo',
			datafield: 'serialNo',
			width: 150,
			pinned: true,
			cellsrenderer: cellsrenderer,
			renderer: columnrenderer
		  },{
			text: 'Staff Name',
			datafield: 'firstName',
			width: 150,
			cellsrenderer: cellsrenderer,
			renderer: columnrenderer
		  }, {
			text: 'Email Id',
			datafield: 'emailId',
			cellsrenderer: cellsrenderer,
			minwidth: 100,
			renderer: columnrenderer
		  },{
			text: 'Job Title',
			datafield: 'staffCategory_Label',
			cellsrenderer: cellsrenderer,
			minwidth: 100,
			renderer: columnrenderer
		  },{
			text: 'Phone Number',
			datafield: 'phoneNumber_Label',
			cellsrenderer: cellsrenderer,
			width: 150,
			renderer: columnrenderer
		  }];
			let vacantParams = {
        apartmentId: parseInt(this.sessionService.apartmentId),
        categoryId: 115,
			}

			this.staffService.getAllStaffsByApartmentIdCategoryId(vacantParams).subscribe((res: any) => {

				let gridSourceData = {
					localdata: res,
					datatype: "array"
				}

				this.userReportDataList = new jqx.dataAdapter(gridSourceData);
				this.isDataLoaded = true;
				this.showItems();
			});


	}

	showItems() {
		this.totalItems = this.userReportDataList["_source"]["localdata"].length;

		if (this.totalItems > this.itemLimit) {
			this.ItemEndIndex = this.itemLimit;
		}
		else {
			this.ItemEndIndex = this.totalItems;
		}

	}

	ngOnInit() {

		this.sharedService.timezonecast.subscribe( timeZone => this.timeZone = timeZone )

		// this.pageName = this.route.params['value'].name;

		this.allLink = this.route.params['value'].link;

		let unitBlockParams = {
			apartmentId: parseInt(localStorage.getItem('apartmentId'))
		}

		this.apartmentService.getApartmentBlockByApartmentId(unitBlockParams).subscribe((res: any) => {
			this.unitBlocksData = res;
		});

		// let params = {
		// 	LookupTypeId: 87,
		// 	ApartmentId: this.sessionService.apartmentId
		// }
		// this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res: any) => {
		// 	var data = res.filter(item => {
		// 		return item.lookupValueId == this.route.params['value'].id
		// 	})
		// 	this.pageName = data[0].lookupValueName; //.replace('/', '');
		// 	this.pageDesp = data[0].description;
		// 	console.log(this.pageName);
		// });

		this.getBlockDetails();


	}


	getAllBlockData(){

		this.filterSelected = 'all';
	  this.singleBlock = "Select Tower";
	  
	  this.blockId = "";
	  this.datagrid.clearfilters();
  
	}

	getSingleBlock(block){
  	
		this.filterSelected = 'single';
		this.singleBlock = block.apartmentBlockNumber;
		this.blockId = block.apartmentBlockId;

		let filtergroup = new jqx.filter();
			let filter_or_operator = 1;
			let filtervalue = this.singleBlock;
			let filtercondition = 'contains';
			let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
			filtergroup.operator = 'or';
			filtergroup.addfilter(filter_or_operator, filterData);
	  
			this.datagrid.showfiltercolumnbackground(false);
	  
			this.columnData.forEach(item => {
			  if(item.datafield != 'Actions') {
				this.datagrid.addfilter(item.datafield, filtergroup, true);
			  }
			})
	  
			this.datagrid.applyfilters();
  
	
  
	}
}

